/* EventDetails.css */

.event-details {
    position: fixed;
    top: 0;
    left: 100%; /* Initially off-screen on the right */
    width: 100%;
    height: 100%;
    background-color: white;
    transition: left 0.3s ease; /* Add a smooth transition */
  }
  
  .event-details.slide-left {
    left: 0; /* Slide in to the screen */
  }
  

  