/* EventList.css */

.event-list-container {
    transition: transform 0.5s ease-in-out;
    overflow: hidden;
  }
  
  .event-list-container.slide-left {
    transform: translateX(-100%);
  }
  
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    margin-bottom: 10px;
  }
  